/* eslint-disable react/prefer-stateless-function */
/* eslint-disable react/prop-types */
// eslint-disable-next-line max-classes-per-file
import React from 'react';
import { v4 as uuidv4 } from 'uuid';
import CKEditor from '@ckeditor/ckeditor5-react';
// eslint-disable-next-line import/no-extraneous-dependencies
import { Map } from 'immutable';
import BallonBlockEditor from '@ckeditor/ckeditor5-build-balloon-block';

class CKEditorWidget extends React.Component {
  currentUid = null;

  usedUids = [];

  openMediaLibrary() {
    const { field, onOpenMediaLibrary, value } = this.props;
    this.currentUid = uuidv4();
    /**
     * `options` hash as a general field property is deprecated, only used
     * when external media libraries were first introduced. Not to be
     * confused with `options` for the select widget, which serves a different
     * purpose.
     */

    this.usedUids.push(this.currentUid);
    return onOpenMediaLibrary({
      controlID: this.currentUid,
      forImage: false,
      privateUpload: field.get('private'),
      value,
      allowMultiple: false,
      config: field.get('media_library', Map()).get('config'),
      field,
    });
  }

  componentDidUpdate() {
    const { mediaPaths, getAsset } = this.props;
    const mediaPath = this.currentUid && mediaPaths.get(this.currentUid);
    if (mediaPath) {
      this.props.onRemoveMediaControl(this.currentUid);
      this.currentUid = null;
      this.editor.model.change((writer) => {
        const imageElement = writer.createElement('image', {
          src: getAsset(mediaPath).url,
          class: `replace-src ${mediaPath}`,
        });

        writer.setAttribute('data-media-path', mediaPath, imageElement);

        // Insert the image in the current selection location.
        this.editor.model.insertContent(
          imageElement,
          this.editor.model.document.selection,
        );
      });
    }
  }

  componentWillUnmount() {
    this.usedUids.forEach((id) => this.props.onRemoveMediaControl(id));
  }

  render() {
    const {
      forID,
      classNameWrapper,
      setActiveStyle,
      setInactiveStyle,
      value,
      onChange,
    } = this.props;
    return (
      <div
        className={classNameWrapper}
        style={{
          paddingLeft: 10,
          paddingRight: 10,
          minHeight: 200,
        }}
      >
        <CKEditor
          id={forID}
          editor={BallonBlockEditor}
          onInit={(editor) => {
            this.editor = editor;
          }}
          config={{
            insertImage: {
              openMediaLibrary: () => {
                this.openMediaLibrary();
              },
            },
          }}
          data={value}
          onChange={(event, editor) => {
            const data = editor.getData();
            onChange(data);
          }}
          onBlur={() => setInactiveStyle()}
          onFocus={() => setActiveStyle()}
          style={{ outline: 'none' }}
        />
      </div>
    );
  }
}

export default CKEditorWidget;
